<template>
    <li style="width: 568px;overflow: auto;">
        <label>
            <input type="checkbox" :checked="todo.ok" @change="handleCheck(todo.id)" />
            <span v-show="!todo.isEdit">{{ todo.tt }}</span>
            <input type="text" v-show="todo.isEdit" ref="inpt" :value="todo.tt" @blur="handleBlur(todo,$event)">
        </label>
        <button class="btn btn-danger" @click="handleDelete(todo.id)">删除</button>
        <button v-show="!todo.isEdit" class="btn btn-edit" @click="handleEdit(todo)">编辑</button>
    </li>
</template>
<script>
    export default {
        name: 'toitem',
        props: ['todo', 'checkTodo', 'deleteTodo'],
        methods: {
            handleCheck(id) {
                this.checkTodo(id)
            },
            handleDelete(id) {
                this.deleteTodo(id)
                // if(confirm("删除？")){this.deleteTodo(id)}
            },
            handleEdit(todo) {
                if (todo.hasOwnProperty('isEdit')) { todo.isEdit = true } else { this.$set(todo, 'isEdit', true) }
                this.$nextTick(function(){
                    this.$refs.inpt.focus()
                })
            },
            handleBlur(todo, e) {
                todo.isEdit = false
                this.$bus.$emit('updateTodo', todo.id, e.target.value)
            },
        }
    }
</script>
<style scoped>
    /*item*/
    li {
        list-style: none;
        height: 36px;
        line-height: 36px;
        padding: 0 5px;
        border-bottom: 1px solid #ddd;
    }

    li label {
        float: left;
        cursor: pointer;
    }

    li label li input {
        vertical-align: middle;
        margin-right: 6px;
        position: relative;
        top: -1px;
    }

    li button {
        float: right;
        display: none;
        margin-top: 3px;
    }

    li:before {
        content: initial;
    }

    li:last-child {
        border-bottom: none;
    }

    li:hover {
        background-color: #ddd;
    }

    li:hover button {
        display: block;
    }
</style>