<template>
    <div id="root">
        <div class="todo-container">
            <div class="todo-wrap">
                <tohead :addTodo="addTodo"></tohead>
                <tolist :todos="todos" :checkTodo="checkTodo" :deleteTodo="deleteTodo"></tolist>
                <tofoot :todos="todos" :checkAllTodo="checkAllTodo" :clearDoneTodo="clearDoneTodo"></tofoot>
            </div>
        </div>
    </div>
</template>
<script>
    import tohead from './components/tohead'
    import tolist from './components/tolist'
    import tofoot from './components/tofoot'

    export default {
        name: 'App',
        components: {
            tohead, tolist, tofoot
        },
        data() {
            return {
                todos: JSON.parse(localStorage.getItem('items')) || [{ id: '1', tt: 'ex.', ok: true },]
                // todos: [
                //     { id: '1', tt: 'example', ok: true },
                // ]
            }
        },
        methods: {
            addTodo(todoObj) {
                this.todos.unshift(todoObj)
            },
            checkTodo(id) {
                this.todos.forEach((e) => {
                    if (e.id == id) e.ok = !e.ok
                })
            },
            updateTodo(id,title) {
                this.todos.forEach((e) => {
                    if (e.id == id) e.tt = title
                })
            },
            deleteTodo(id) {
                this.todos = this.todos.filter((e) => e.id !== id)
            },
            checkAllTodo(ok) {
                this.todos.forEach((e) => {
                    e.ok = ok
                })
            },
            clearDoneTodo() {
                this.todos = this.todos.filter((e) => {
                    return !e.ok
                })
            }
        },
        watch: {
            todos: {
                deep: true,
                handler(values) {
                    localStorage.setItem('items', JSON.stringify(values))
                },
            }
        },
        mounted() {
            this.$bus.$on('updateTodo',this.updateTodo)
        },
        beforeDestroy() {
            this.$bus.$off('updateTodo')
        },
    }
</script>
<style>
    /*base*/
    body {
        background: #fff;
    }

    .btn {
        display: inline-block;
        padding: 4px 12px;
        margin-bottom: 0;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        vertical-align: middle;
        cursor: pointer;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2),
            0 1px 2px rgba(0, 0, 0, 0.05);
        border-radius: 4px;
    }

    .btn-danger {
        color: #fff;
        background-color: #da4f49;
        border: 1px solid #bd362f;
    }

    .btn-danger:hover {
        color: #fff;
        background-color: #bd362f;
    }

    .btn-edit {
        color: #000;
        background-color: #fff;
        border: 1px solid #004080;
        margin-right: 10px;
    }

    .btn-edit:hover {
        color: #fff;
        background-color: #004080;
    }

    .btn:focus {
        outline: none;
    }

    .todo-container {
        width: 600px;
        margin: 0 auto;
    }

    .todo-container .todo-wrap {
        padding: 10px;
        border: 1px solid #ddd;
        border-radius: 5px;
    }
</style>