<template>
    <!-- <div class="todo-footer" v-show="total"> -->
    <div class="todo-footer" v-show="todos.length">
        <label>
            <input type="checkbox" v-model="isAll"/>
        </label>
        <span>
            <span>完成: {{ doneTotal }}</span> , 共有: {{ todos.length }}
        </span>
        <button class="btn btn-danger" @click="clearDone">清除勾选任务</button>
    </div>
</template>
<script>
    export default {
        name: 'tofoot',
        props: ['todos','checkAllTodo', 'clearDoneTodo'],
        computed: {
            doneTotal() {
                return this.todos.reduce((pre, cur) => {
                    return pre + (cur.ok ? 1 : 0)
                }, 0)
            // this.todos.reduce((pre, cur) =>pre + (cur.ok ? 1 : 0), 0)   
            }, 
            isAll:{
                get(){
                    return this.doneTotal == this.todos.length && this.todos.length >0
                },
                set(e){
                    this.checkAllTodo(e)
                },
            }
        },
        methods: {
            clearDone(){
                this.clearDoneTodo()
            }
        },
    }
</script>
<style scoped>
    /*footer*/
    .todo-footer {
        height: 40px;
        line-height: 40px;
        padding-left: 6px;
        margin-top: 5px;
    }

    .todo-footer label {
        display: inline-block;
        margin-right: 20px;
        cursor: pointer;
    }

    .todo-footer label input {
        position: relative;
        top: -1px;
        vertical-align: middle;
        margin-right: 5px;
    }

    .todo-footer button {
        float: right;
        margin-top: 5px;
    }
</style>